<template>

  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/mainTwo'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>重要信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>修改信息</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">
      <el-form :model="userForm" :rules="userFormRules" ref="userFormRef" label-width="120px">
        <el-form-item label="管理员账号" prop="userName">
          <el-input v-model="userForm.userName" style="width: 400px" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="管理员密码" prop="userPassword">
          <el-input v-model="userForm.userPassword" type="password" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="管理员性别" prop="userSex">
          <el-input v-model="userForm.userSex" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="userPhone">
          <el-input v-model="userForm.userPhone" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" prop="userEmail">
          <el-input v-model="userForm.userEmail" style="width: 400px"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="open">确认修改</el-button>
        </el-form-item>

      </el-form>
    </el-card>

  </div>
</template>

<script>
  export default {
    name: "editPage",
    data() {
      return {
        userName: sessionStorage.getItem("userName"),
        userForm: {
          id: "",
          userName: "",
          userPassword: "",
          userSex: "",
          userPhone: "",
          userEmail: ""
        },
        userFormRules: {
          userName: [
            {required: true, message: '不可为空', trigger: 'blur'},
            {min: 1, max: 20, message: '字符长度在2~20个字符之间', trigger: 'blur'}
          ],
          userPassword: [
            {required: true, message: '不可为空', trigger: 'blur'}
          ],
          userSex: [
            {required: true, message: '不可为空', trigger: 'blur'}
          ],
          userPhone: [
            {required: true, message: '不可为空', trigger: 'blur'}
          ],
          userEmail: [
            {required: true, message: '不可为空', trigger: 'blur'}
          ]
        },
      }
    },
    created() {
      this.queryUser()
    },
    methods:{
      queryUser() {
        this.$http.get('user/getUserByName',
          {params: {userName:this.userName}})
          .then(res => {
            this.userForm = res.data.data;
          });
      },
      editUser(){
        this.$refs.userFormRef.validate(async valid =>{
          if(!valid) return
          const {data:res}=await this.$http.post("user/editUser",this.userForm)
        })
      },
      open() {
        this.$confirm('此操作将不可逆, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.editUser();
          this.$message({
            type: 'success',
            message: '修改成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消修改'
          });
        });
      }
    }
  }
</script>

<style scoped lang="scss">

  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }
  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
    width: 680px;
  }
</style>



